@charset "UTF-8";
@font-face {
  font-family: 'TTLakes';
  font-weight: 400;
  font-style: normal;
  src: url(../fonts/TTLakes-Regular.woff) format("woff"); }

@font-face {
  font-family: 'TTLakes';
  font-weight: 500;
  font-style: normal;
  src: url(../fonts/TTLakes-Medium.woff) format("woff"); }

@font-face {
  font-family: 'TTLakes';
  font-weight: 700;
  font-style: normal;
  src: url(../fonts/TTLakes-Bold.woff) format("woff"); }

/*****common styles*****/
html {
  box-sizing: border-box; }

*,
*::after,
*::before {
  box-sizing: inherit; }

h1,
h2,
h3,
h4,
p {
  margin: 0;
  padding: 0; }

body {
  min-width: 320px;
  margin: 0;
  overflow-x: hidden;
  background-color: #f8f8f8;
  font-family: 'TTLakes';
  font-weight: 400;
  color: #1b1c21;
  letter-spacing: -0.2px; }

ul {
  margin: 0;
  padding: 0;
  list-style: none; }

li {
  display: inline-block; }

a {
  text-decoration: none;
  color: inherit; }

button {
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  padding: 0;
  margin: 0;
  cursor: pointer; }

.order-send-btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  -moz-column-gap: 15px;
       column-gap: 15px;
  padding: 4px 15px;
  color: #fff;
  font-size: 12px;
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;
  border-radius: 16px;
  background-color: #FF3E79; }

.heading {
  font-family: 'TTLakes';
  font-weight: 700; }

.more-button {
  display: inline-block;
  border: none;
  background-color: transparent;
  padding: 0;
  font-family: 'TTLakes';
  font-weight: 500;
  font-size: 1rem;
  line-height: 24px;
  color: #1b1c21;
  letter-spacing: -0.2px;
  margin-top: 16px;
  margin-left: 10px;
  cursor: pointer; }

.more-icon {
  display: inline-block;
  background-image: url(../img/more-icon.svg);
  background-repeat: no-repeat;
  background-size: contain;
  width: 8px;
  height: 10px; }

.menu-icon rect {
  transition: all 0.3s ease-in-out; }

.menu-icon:hover rect {
  fill: #ffd0de; }

.menu-icon:hover path {
  fill: #ff3e79; }

a,
button,
label:focus {
  outline-color: #FF3E79; }

.section-header {
  padding: 16px;
  background-image: linear-gradient(to right, #f8f8f8 0%, #ffffff 98.61%);
  border-top: 1px solid #d9fff5; }

.section-heading {
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  letter-spacing: -0.2px;
  color: #7e7e82;
  text-transform: uppercase; }

.gradient {
  background: linear-gradient(to right, transparent, #f8f8f8 90%, #f8f8f8 0.01%); }

.order-send-btn {
  transition: all .3s ease; }

.order-send-btn:hover {
  color: #FF3E79;
  background-color: #ffd0de; }

.order-send-btn:hover span svg path {
  fill: #FF3E79; }

