/*****mobile-menu(width: 320px)*****/
::-webkit-scrollbar {
  width: 0; }

.mobile-menu {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  z-index: -1; }

.mobile__wrapper {
  opacity: 0;
  min-width: 320px;
  background-color: #fff;
  top: 0;
  left: -320px;
  transition: left .5s ease; }

.mobile__wrapper.mobile-menu--open {
  left: 0;
  opacity: 1;
  position: fixed;
  overflow-y: auto;
  box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2);
  z-index: 1000; }

.mobile__wrapper.mobile-menu--open::-webkit-scrollbar {
  width: 0;
  display: none; }

.mobile-menu.mobile-wrapper::-webkit-scrollbar {
  width: 0;
  display: none; }

.mobile__background {
  position: relative;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.95); }

.mobile__container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0 16px;
  padding-bottom: 32px; }

.mobile__header {
  min-height: 88px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.mobile__side-wrapper {
  display: flex;
  align-items: center;
  -moz-column-gap: 16px;
       column-gap: 16px; }

.mobile__navigation {
  margin-bottom: auto; }

.mobile__navigation-list {
  display: flex;
  flex-direction: column;
  row-gap: 24px; }

.mobile__navigation-link {
  position: relative;
  color: #1b1c21;
  font-size: 1rem;
  line-height: 24px;
  font-weight: 500; }

.mobile__navigation-link::before {
  content: '';
  position: absolute;
  top: -5px;
  left: -20px;
  width: 8px;
  height: 32px;
  background-color: #41F6D7;
  border-radius: 4px;
  align-self: center;
  opacity: 0;
  transition: opacity 0.5s ease; }

.mobile__navigation-link:hover {
  text-decoration: underline; }

.mobile__navigation-link:hover::before {
  opacity: 1; }

.mobile__navigation-link--active {
  color: #7e7e82; }

.mobile__navigation-link--active::before {
  opacity: 1; }

.mobile__navigation-link--active:hover {
  text-decoration: none;
  cursor: default; }

.mobile__footer {
  padding-top: 50px; }

.mobile__footer a:hover {
  text-decoration: underline; }

.mobile__footer-buttons {
  display: flex;
  -moz-column-gap: 16px;
       column-gap: 16px;
  padding-bottom: 16px; }

.mobile__footer-connection {
  display: flex;
  flex-direction: column;
  row-gap: 12px;
  padding-bottom: 8px; }

