/*****brands, brands__swiper(width: 320px)*****/
.brands__more-button,
.brands__less-button {
  display: none; }

.brands {
  padding-bottom: 24px;
  position: relative;
  overflow: hidden; }
  .brands__header {
    margin-bottom: 16px; }

.swiper {
  margin: 0;
  position: relative; }

.swiper__item {
  box-sizing: border-box;
  width: 240px;
  height: 72px;
  background-color: white;
  border: 1px solid #eaeaea;
  border-radius: 6px; }

.swiper__link {
  width: 100%;
  height: 100%;
  padding: 0 16px;
