  justify-content: space-between;
  align-items: center; }

.swiper__link .more-arrow {
  transition: transform 0.3s ease; }

.swiper__link .more-arrow rect {
  transition: all 0.1s ease-in; }

.swiper__link:hover .more-arrow rect {
  fill: #ffd0de;
  stroke: #ffd0de; }

.swiper-wrapper {
  display: inline-flex;
  box-sizing: content-box;
  left: 16px; }

.brands__swiper::-webkit-scrollbar {
  width: 0;
